.header {
    width: 100vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* background: red; */
    padding: 1rem 2rem;
}

.header__left {
    display: flex;
    justify-content: center;
    align-items: center;
}

.header__logo img {
    width: 200px
}


.header__links {
    margin-left: 100px;
}

.header__links ul {
    display: flex;
    justify-content: center;
    align-items: center;
}

.header__links li {
    list-style-type: none;
    margin-left: 30px;
}

.header__links li a {
    text-decoration: none;
    color: #FFF;
}


.header__right {
    display: flex;
    justify-content: center;
    align-items: center;
}

.header__signout {
    margin-left: 50px;
    color: red;
    font-size: 2rem;
    cursor: pointer;
    transition: .2s ease-in-out;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


.header__signout p {
    font-size: 1rem;
}

.header__signout:hover {
    transform: translateY(-5px);
}

.header__signout:active {
    transform: scale(.9);
}