.loginProfile {
    min-height: 100vh;
}

.loginProfile__body {

    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.loginProfile__title {
    font-size: 3rem;
    margin-top: -100px;
}

.loginProfile__inputContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin-top: 50px;
}

.loginProfile__inputContainer input {
    width: 100px;
    height: 100px;
    padding: 20px;
    font-size: 3rem;
    text-align: center;
    background: rgba(142, 142, 142, 0.085);
    border: 2px solid rgba(255, 255, 255, .2);
    color: #fff;
}

.loginProfile__error {
    margin-top: 20px;
    color: red;

}