.createProfile {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.createProfile__container {
    padding: 2rem 3rem;
    border: 2px solid rgba(255, 255, 255, .2);
    width: 650px;
    border-radius: 2px;
}


.createProfile__title {
    font-size: 3rem;
    font-weight: 400;
}

.createProfile__subtitle {
    font-size: 1rem;
    font-weight: 400;
    color: #ccc;
    margin-bottom: 40px;
}

.createProfile__form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.createProfile__image {
    cursor: pointer;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: .1s ease-in-out;
}

.createProfile__image img {
    border-radius: 1px;

}

.createProfile__image:active {
    transform: scale(.98);
}



.createProfile__image p {
    opacity: .6;
    transition: .3s ease-in-out;
    font-size: 15px;
    margin-top: 5px;
    user-select: none;
}

.createProfile__image:hover p {
    opacity: 1;
}


.createProfile__input {
    margin-top: -50px;
    display: flex;
    flex-direction: column;
    align-items: start;
    width: 58%;
}

.createProfile__input>div {
    width: 100%;
}


.createProfile__input input {
    width: 100%;
}




.createProfile__checkbox {
    user-select: none;
    color: #999;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 5px;
    position: relative;
}



.createProfile__checkbox input {
    height: 15px;
    width: 15px;
    -webkit-appearance: none;
    -moz-appearance: none;
    -o-appearance: none;
    appearance: none;
    border-radius: 2px;
    outline: none;
    transition-duration: 0.3s;
    background-color: #555;
    bordeR: none;
    margin-right: 10px;
}

.createProfile__checkbox label {
    font-size: 13px;
    font-weight: 400;
}

.createProfile__checkbox>input:checked {
    background-color: #777;
    /* background: red; */
}

.createProfile__checkbox>input:checked+label::before {
    content: '\2714';
    font-size: 14px;
    display: block;
    text-align: center;
    color: #000;
    position: absolute;
    left: 2px;
    top: -1px;
    z-index: 999;
    font-weight: bold;
    transform: rotateZ(15deg);
    z-index: 1;

}

.createProfile__checkbox>input:active {
    border: 2px solid #34495E;
}


.createProfile__buttons {
    margin-top: 50px;
}

.createProfile__buttons button {
    padding: 10px 30px;
    font-size: 18px;
    cursor: pointer;
    border: none;
    outline: none;
    margin-right: 10px;
    background: rgba(229, 9, 20, 1);
    color: #eee;
    border-radius: 2px;
    transition: .3s ease-in-out;
}

.createProfile__buttons button:hover {
    background: rgb(201, 8, 18);

}

.createProfile__buttons button:nth-child(2) {
    background: transparent;
    border: 2px solid rgba(255, 255, 255, .2)
}

.createProfile__buttons button:nth-child(2):hover {
    background: rgba(255, 255, 255, .2);
    border-color: transparent;
}

.createProfile__buttons button:nth-child(2):active {
    background: rgba(255, 255, 255, .3) !important;
}