.footer {
    padding: 3rem clamp(1rem, 10vw, 10rem);
    /* padding-bottom: 1rem; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* background: red; */
    /* position: fixed; */
    bottom: 0;
    left: 0;
    width: 100vw;
    border-top: 3px solid rgba(255, 255, 255, .05);
    margin-top: 50px;

}

.footer__center {
    text-align: center;
    font-weight: 700;
    color: #999
}

.footer a {
    color: #999;
    margin-right: 5px;

}

.footer a svg {
    transition: .3s ease-in-out;
    font-size: 2rem;

}

.footer__right a svg {
    font-size: 5rem;
}


.footer a:hover svg {
    color: #ddd;
    transform: translateY(-5px);
}